/* Normalize */

* {
  font-family: 'Open Sans', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Some configuration */

b {
  font-family: "Droid Sans";
}

h1, h2, h3, h4 {
  font-family: "Droid Sans";
}

body {
  color: #444;
  width: 100%;
  font-size: 16px;
}

.wrapper {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  background: #d0d0d0;
}

/* Header */ 

.headerSection {
  width: 100%;
  background: #111;
  display: flex;
  flex-wrap: wrap;
}

/* Icon menu bar */
.headerSection .menuBar {
  width:100%;
}

.headerSection .menuBar i {
  color: #fff;
  padding: 1.5em;
  transition: all .3s;
}

.menuBar i:hover {
  color:#2abfe4;
  transition: all .3s;
  cursor: pointer;
}

/* Menu navigation */
.headerSection .menu {
  width: 100%;
  display: none;
}

.headerSection .menu ul {
  display: flex;
  flex-wrap: wrap;
}

.headerSection .menu ul li {
  width: 100%;
  list-style: none;
}

.headerSection .menu ul li a {
  background-color: #111;
  font-family: "Droid Sans";
  display: block;
  padding: 1.5em;
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  transition: all .3s;
}

.menu ul li a i {
  margin-right: 0.5em;
}

.menu ul li a:hover {
  color:#2abfe4;
  transition: all .3s;
}

/* Presentation card */

.presentationCardSection {
  color: #fff !important;
  width: 80%;
  margin: 1em auto;
  background: linear-gradient(-20deg, #485b68, #465358, #151718, #504872);
  background-size: 400% 400%;
  animation: gradient 20s ease infinite;
  padding-top: 3em;
  user-select: none;
  border-radius: 1em;
  -webkit-box-shadow: 0px 0px 27px -1px rgba(0,0,0,0.36); 
  box-shadow: 0px 0px 27px -1px rgba(0,0,0,0.36);
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}

img {
  user-select: none;
}

/* Gradient's presentation card */
@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
.presentationCardSection .titlePresentation {
  text-align: center;
  padding: 1em;
  padding-bottom: 1.5em;
}

.titlePresentation h1 {
  font-size: 45px;
}

.presentationCardSection .photoPresentation {
  width: 20%;
  margin: 0 auto;
  overflow: hidden;
}

.photoPresentation img {
  display: block;
  margin: auto;
  width: 100%;
  object-fit: cover;
  border-radius: 50%;
}

.presentationCardSection .descriptionPresentation {
  width: 60%;
  margin: 0 auto;
  text-align: center;
  padding: 2em 1em 2.5em;
}

/* Abilities section */

.abilitiesSections {
  margin: 0 auto;
  width: 100%;
}

.abilitiesBox, .projectBox {
  width: 80%;
  margin: 1em auto;
  -webkit-box-shadow: 0px 0px 27px -1px rgba(0,0,0,0.36); 
  box-shadow: 0px 0px 27px -1px rgba(0,0,0,0.36);
}

.abilitiesBox .orangeBox {
  padding: 1em;
  width: 100%;
  background: rgb(190,109,17);
  background: linear-gradient(90deg, rgba(190,109,17,1) 0%, rgba(223,112,22,1) 49%, rgba(255,114,0,1) 100%);
}

.abilitiesBox .abilitiesTitle, .projectTitle {
  width: 100%;
  text-align: center;
  padding: 1em 1em 2em 1em;
  background: #ededed;
  color: #444;
}

.abilitiesTitle h1, .projectTitle h2 {
  padding-top: 1em;
  font-size: 35px;
  margin-bottom: 0.5em;
}

/* Each abilitie card */

.abilitiesCard {
  margin: 0 auto;
  width: 80%;
  display: flex;
  flex-wrap: wrap;
}

.abilitiesCard .card {
  text-align: center;
  margin: 0 auto 1em;
  background: #ededed;
  padding: 1em;
  width: 30%;
  border-radius: 0 1em 0 1em;
  transition: all .7s;
  -webkit-box-shadow: 0px 0px 27px -1px rgba(0,0,0,0.36); 
  box-shadow: 0px 0px 27px -1px rgba(0,0,0,0.36);
}

.card .imgCard {
  width: 100%;
  overflow: hidden;
  object-fit: cover;
  border: 1px solid #d0d0d0;
  background: #fff;
  border-radius: 0.5em;
}

.htmldiv {
  padding: 1em;
}

.card .titleCard {
  margin-top: 1em;
}

.card .imgCard img {
  display: block;
  margin: auto;
  width: 100%;
  object-fit: cover;
  border-radius: 0.5em;
}

.card .descriptionCard {
  padding: 1em;
  padding-top: 0.5em;
  text-align: justify;
}

/* Project section */

.projectSection {
  width: 100%;
}

.projectSection .projectBox .greenBox {
  padding: 1em;
  background: rgb(72,158,45);
  background: linear-gradient(90deg, rgba(72,158,45,1) 0%, rgba(92,191,62,1) 49%, rgba(81,207,41,1) 100%);
}

/* Web development project */

.eachProject {
  width: 80%;
  margin: 1em auto 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 2em;
  background: rgb(38,38,38);
  color: #fff;
  border-radius: 1em;
  -webkit-box-shadow: 0px 0px 27px -1px rgba(0,0,0,0.36); 
  box-shadow: 0px 0px 27px -1px rgba(0,0,0,0.36);
}

.eachProject .project {
  margin: 1em;
  width: 60%;
}

.bigTitleProject h2 {
  margin-bottom: 0.5em;
  font-size: 25px;
}

.eachProject .imgProject {
  margin: 1em;
  width: 30%;
  overflow: hidden;
  object-fit: cover;
}

.imgProject img {
  width: 40%;
  display: block;
  margin: auto;
}

/* Other projects card */

.simpleCard {
  padding: 2em;
  margin: 1em auto;
  width: 80%;
  background: rgb(198,192,183);
  background: linear-gradient(90deg, rgba(198,192,183,1) 0%, rgba(191,180,165,1) 50%, rgba(222,213,200,1) 100%);
  border-radius: 1em;
  color: #444;
  -webkit-box-shadow: 0px 0px 27px -1px rgba(0,0,0,0.36); 
  box-shadow: 0px 0px 27px -1px rgba(0,0,0,0.36);
}

.simpleCard .titleSimpleCard {
  width: 100%;
  padding-bottom: 0.5em;
}

/* Contact section */

.contactSection {
  width: 100%;
}

.stripeBox {
  width: 80%;
  margin: 1em auto;
  -webkit-box-shadow: 0px 0px 27px -1px rgba(0,0,0,0.36); 
  box-shadow: 0px 0px 27px -1px rgba(0,0,0,0.36);
}

.stripe.blue {
  padding: 1em;
  background: rgb(34,28,122);
  background: linear-gradient(90deg, rgba(34,28,122,1) 0%, rgba(67,77,161,1) 43%, rgba(121,148,207,1) 100%);
}

/* Social network side */

.stripe.yellow {
  padding: 1em;
  background: rgb(187,190,53);
  background: linear-gradient(90deg, rgba(187,190,53,1) 0%, rgba(222,214,31,1) 43%, rgba(255,248,0,1) 100%);
}

.boxContent {
  padding: 2em;
  background: #ededed;
}

.boxContent .boxTitle {
  padding-bottom: 0.5em;
}

.boxContent .boxDesc {
  padding-top: 0;
}

/* List of social network */

.boxDesc li.element {
  margin: 0.5em;
  margin-left: 0;
  list-style-type: none;
}

.element i {
  margin-right: 0.5em;
}

.element a.link {
  text-decoration: none;
  color: #444;
}

.element a.link:hover {
  text-decoration: underline;
}

/* Footer section */

.footerSection {
  width: 100%;
}

.footerSection .developedWith {
  background: #333;
  padding: 1em;
}

.developedWith .centered-box {
  padding: 1em;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80%;
  margin: 0 auto;
}

.centered-box p {
  font-family: "Droid Sans";
  font-weight: bold;
  color: #fff;
}

.footerSection .developedWith i {
  margin-left: 0.5em;
  color: #2abfe4;
}

.footerSection .centered-box p span.react {
  font-family: "Droid Sans";
  color: #2abfe4;
}

.footerSection .aboutThisPage {
  padding: 2em;
  background: #111;
  color: #fff;
  display: flex;
  justify-content: center;
}

.footerSection .aboutThisPage .parraph b, .parraph {
  font-family: "Droid Sans", "Segoe UI";
}

.footerSection .socialMediaIcons {
  background: #333;
  display: flex;
  justify-content: center;
  padding: 1em 2em;
}

.socialMediaIcons a.link {
  color: #fff;
  text-decoration: none;
  margin: 1em;
  transition: all .3s;
}

.socialMediaIcons a.link:hover {
  color: #2abfe4;
  transition: all .3s;
}

/* Some config for Phone */

@media only screen and (max-width:640px) {
  .presentationCardSection .descriptionPresentation {
    width: 90%;
    text-align: justify;
  }
}

/* Tablet */

@media only screen and (max-width:900px) {
  .abilitiesBox, 
  .presentationCardSection, 
  .abilitiesCard .card,
  .projectBox,
  .eachProject,
  .simpleCard, .stripeBox {
    width: 90%;
  }

  .abilitiesCard {
    width: 100%;
  }

  .abilitiesCard .card {
    border-radius: 1em;
  }

  .imgProject img {
    width: 100%;
  }
}